<template>
	<za-page-content header="推荐算法参数配置" scroll="true">
		<el-row class="math-box">
			<el-row class="tit">用户行为权重</el-row>
			<el-row class="input-list">
				<el-row  class="item">
					<el-col :span="4" class="txt">点击阅读</el-col>
					<el-col :span="6">
						<el-input-number :controls="false" v-model="defaultData.viewsWeight" style="width: 100%"></el-input-number>
					</el-col>
				</el-row>
				<el-row  class="item">
					<el-col :span="4" class="txt">喜欢</el-col>
					<el-col :span="6">
						<el-input-number :controls="false" v-model="defaultData.likeWeight" style="width: 100%"></el-input-number>
					</el-col>
				</el-row>
				<el-row  class="item">
					<el-col :span="4" class="txt">不喜欢 </el-col>
					<el-col :span="6">
						<el-input-number :controls="false" v-model="defaultData.dislikeWeight" style="width: 100%"></el-input-number>
					</el-col>
				</el-row>
				<el-row  class="item">
					<el-col :span="4" class="txt">分享</el-col>
					<el-col :span="6">
						<el-input-number :controls="false" v-model="defaultData.shareWeight" style="width: 100%"></el-input-number>
					</el-col>
				</el-row>
				<el-row  class="item">
					<el-col :span="4" class="txt">关闭</el-col>
					<el-col :span="6">
						<el-input-number :controls="false" v-model="defaultData.closeWeight" style="width: 100%"></el-input-number>
					</el-col>
				</el-row>
			</el-row>
			<el-row class="tit">资讯分类初始热度</el-row>
			<el-row class="input-list">
				<el-row  class="item" :key="index" v-for="(item, index) in defaultData.directoryWeightList">
					<el-col :span="4" class="txt">{{ getValue(item.directoryId) }}</el-col>
					<el-col :span="6">
						<el-input-number :controls="false" v-model="item.iniHeat" style="width: 100%"></el-input-number>
					</el-col>
				</el-row>
			</el-row>
			<el-row>
				<el-button @click="submit" type="success">保存</el-button>
			</el-row>
		</el-row>
	</za-page-content>
</template>

<script>
	import {ajaxGetCommon, ajaxPostCommon} from '../../api/common.js'
	import * as ajaxUrl from '../../api/url'

	export default {
		data() {
			return {
				subData: {
					viewsWeight: '',
					likeWeight: '',
					dislikeWeight: '',
					shareWeight: '',
					closeWeight: '',
					directoryWeightList: [],
				},
				defaultData: {

				},
				tags: []
			};
		},
		mounted() {
			this.getTags();

		},
		methods: {
			getTags () {
				ajaxPostCommon(ajaxUrl.listInfoDirectory, {pageNum: 1, pageSize: 10}).then((data) => {
					console.log(data)
					if (data.returnCode === '200') {
						this.tags = data.result;
						this.getMsg();
					}
				})
			},
			getMsg() {
				ajaxPostCommon(ajaxUrl.getInfoBehaviorWeightDetail).then((data) => {
					if (data.returnCode === '200') {
						this.defaultData = data.result;
					}
				});
			},
			getValue(key) {
				let a =  this.tags.find((item) => {
					return item.id === key
				})
				return a ? a.directoryName :  '';

			},
			submit() {;
				console.log(this.defaultData);
				let _this = this;
				//let params = {...this.defaultData};
				let params = {
					id: this.defaultData.id,
					viewsWeight: this.defaultData.viewsWeight,
					likeWeight: this.defaultData.likeWeight,
					dislikeWeight: this.defaultData.dislikeWeight,
					shareWeight: this.defaultData.shareWeight,
					closeWeight: this.defaultData.closeWeight,
					directoryWeightList: []
				}
				let arr = this.defaultData.directoryWeightList.map((item) => {
					let obj = {
						directoryId: item.directoryId,
						intHeat: item.intHeat
					}
					return obj;
				});
				params.directoryWeightList = arr;
				console.log(params);
				//return
				$.ajax({
					url: `https://mgw-daily.zhongan.com/appapi/za-dm-news/${ajaxUrl.updateInfoBehaviorWeight}`,
					type: 'POST',
					contentType: "application/json;charset=utf-8",
					data: JSON.stringify(params),
					success: function (data) {
						if (data.returnCode === '200') {
							_this.$message({
								message: '保存成功~',
								type: 'success'
							});
						} else {
							_this.$message({
								message: data.returnMsg,
								type: 'error'
							});
						}
					}
				});
			}
		}
	};
</script>

<style lang="scss" scoped>
	.math-box {

		.tit {
			padding-top: 20px;
			font-size: 18px;
			font-weight: 600;
			color: #464646;
		}
		.input-list {
			.item {
				display: flex;
				align-items: center;
				width: 50%;
				margin-top: 20px;
				.txt {
					font-size: 14px;
					font-weight: 400;
				}
			}

		}
	}
</style>



